.Login {
  height: 100vh;
  position: relative;
  background-color: #e4e4e4;
  background-size: cover;
  background-position: center;
  opacity: 3rem;
  margin: 0 -4rem;
}

@font-face {
  font-family: Para;
  src: url(/randomApp/static/media/Codedemo1.eaa1b4b3.otf) format("opentype");
}
.LoginComp {
  height: 40rem;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#eb3349), to(#f45c43));
  background-image: -webkit-linear-gradient(top, #eb3349, #f45c43);
  background-image: linear-gradient(to bottom, #eb3349, #f45c43);
  box-shadow: 1px 1.2px 5px #000;
  width: 30rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 5px 5px 5px 5px;
}

@media only screen and (max-width: 450px) {
  .LoginComp {
    width: 28rem;
    height: 38rem;
  }
}

@media only screen and (max-width: 380px) {
  .LoginComp {
    width: 24rem;
    height: 34rem;
    left: 54%;
  }
}

@media only screen and (max-width: 340px) {
  .LoginComp {
    left: 49%;
  }
}

@media only screen and (max-width: 320px) {
  .LoginComp {
    height: 32rem;
    width: 22rem;
  }
}

.LoginForm {
  display: flex;
  flex-direction: column;
  margin-top: 15rem;
  width: 80%;
  margin-left: 3rem;
}

.welcome {
  position: absolute;
  top: 8rem;
  left: 12.8rem;
}

.welcomeIcon {
  font-size: 4rem;
}

.loginHeader {
  position: absolute;
  text-transform: uppercase;
  color: #fff;
  top: 0;
  left: 10.8rem;
  font-weight: 500;
  font-size: 2.4rem;
  letter-spacing: 3px;
  font-family: Para;
}
@media only screen and (max-width: 380px) {
  .loginHeader {
    left: 7rem;
  }
}

input {
  border: none;
  background-color: transparent;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  color: #fff;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  padding: 0.8rem 2rem;
  font-size: 1.1rem;
  font-family: "Arvo", serif;
}

input:focus {
  outline: none;
}

@media only screen and (max-width: 450px) {
  input {
    padding: 1rem 2rem;
  }
}

input::-webkit-input-placeholder {
  color: rgb(226, 225, 225);
  font-size: 1.04rem;
  padding: 0 1rem;
}

input::-moz-placeholder {
  color: rgb(226, 225, 225);
  font-size: 1.04rem;
  padding: 0 1rem;
}

input::-ms-input-placeholder {
  color: rgb(226, 225, 225);
  font-size: 1.04rem;
  padding: 0 1rem;
}

input::placeholder {
  color: rgb(226, 225, 225);
  font-size: 1.04rem;
  padding: 0 1rem;
}

input:focus::-moz-placeholder {
  color: transparent;
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}

.loginbtn {
  position: absolute;
  top: 74%;
  left: 21.5%;
  border: none;
  background-color: transparent;
  padding: 1.3rem 6.5rem;
  border-radius: 100px;
  background-color: #fff;
  cursor: pointer;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  text-transform: uppercase;
  font-family: "Arvo", serif;
  font-size: 1.2rem;
}

@media only screen and (max-width: 380px) {
  .loginbtn {
    top: 77%;
    left: 15%;
  }
}

@media only screen and (max-width: 320px) {
  .loginbtn {
    left: 11%;
    top: 81%;
  }
}

.loginbtn::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
  background-color: #fff;
  border-radius: 100px;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  z-index: -1;
}

.loginbtn:hover::after {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  opacity: 0;
}

.loginspan {
  font-size: 1rem;
  color: rgb(153, 150, 150);
}
.loader {
  position: absolute;
  z-index: 40000;
  top: 50%;
  left: 50%;
}

button:disabled {
  cursor: not-allowed;
}

